* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color: white;

}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  color: white;
}
table {
  margin: 10px ;
  width: 100%;
}

.logoHeading img {
  width: 10%;
  margin: auto;
  border-radius:10%;
  display: block;
}

.app{
  background-image: url('./assets/Thunder.jpg');
  background-size: cover;
  background-position: top;
  transition: 0.4s ease-out;
}

.app.warm{
  background-image: url('./assets/Sunny.jpg');
  background-size: cover;
  background-position: center;

}

main{
  min-height: 100vh;
  background-image: linear-gradient(to bottom, rgba(0,0,0,0.2), rgba(0, 0, 0, 0.75));
  padding: 25px;
}

.search-box {
  width: 100%;
  color: white;
}

.search-box .search-bar {
  display: block;
  width: 100%;
  padding: 15px;
  appearance: none;
  background: none;
  outline: none;
  background-color: rgba(255,255,255, 0.5);
  /* border-color: whitesmoke; */
  border-radius: 16px 16px 16px 16px;
  margin-top: 25px;
  box-shadow: 0px 5px rgba(0, 0, 0, 0.2);
  color: white;
  font-size: 20px;
  transition: 0.4s ease;
}

.search-box .search-bar :focus {
  background-color: rgba(255,255,255 0.5);
}

.location-box .location {
  color: #fff;
  font-size: 32px;
  font-weight: 500;
  text-align: center;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}

.location-box .date {
  color: #fff;
  font-size: 20px;
  font-weight: 300;
  font-style: italic;
  text-align: center;
  text-shadow: 2px 2px rgba(50, 50, 70, 0.5);
}

.weather-box {
  text-align: center;
}

.weather-box .temp {
position: relative;
display: inline-block;
margin: 30px auto;
background-color: rgba(255, 255, 255, 0.2);
border-radius: 16px;
padding: 15px 25px;
color: #fff;
font-size: 102px;
font-weight: 900;
text-shadow: 3px 6px rgba(50, 50, 70, 0.5);
text-align: center;
box-shadow: 3px 6px rgba(0, 0, 0, 0.2);
}
.weather-box .temp-min {
  position: relative;
  display: inline-block;
  margin: 10px;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 16px;
  padding: 15px 25px;
  color: #B5EBF5;
  font-size: 30px;
  font-weight: 900;
  text-shadow: 3px 6px rgba(50, 50, 70, 0.5);
  text-align: center;
  box-shadow: 3px 6px rgba(0, 0, 0, 0.2);
  }

  .weather-box .temp-max {
    position: relative;
    display: inline-block;
    margin: 10px;
    background-color: rgba(255, 255, 255, 0.2);
    border-radius: 16px;
    padding: 15px 25px;
    color: #FD9162;
    font-size: 30px;
    font-weight: 900;
    text-shadow: 3px 6px rgba(50, 50, 70, 0.5);
    text-align: center;
    box-shadow: 3px 6px rgba(0, 0, 0, 0.2);
    }
.weather-box .weather {
  color: #fff;
  font-size: 48px;
  font-weight: 700;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}

.humidity {
  color: #fff;
  font-size: 38px;
  font-weight: 600;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}
